@import "settings/colors";
@import "settings/fonts";

.footer {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  .footer__copyright {
    font-size: 18px;
  }

  .footer__socials {
    height: 100%;
    width: 80px;
    position: absolute;
    right: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

}