@import "settings/colors";
@import "settings/fonts";

.contact {
  height: 1026px;
  background: linear-gradient(rgba(255,255,255,0.8) 0%,rgba(255,255,255,0.8) 100%), url(../assets/Background-Contact-Form.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contact__container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.contact__form {
  width: 528px;
  height: 986px;
  margin-right: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact__fields {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.contact__fields label {
  display: flex;
  flex-direction: column;
  width: 250px;
}


.contact__fields input,
.contact__fields textarea {
  border: none;
  border-bottom: 1px solid $color-lightgray;
  background-color: transparent;
  font-size: 18px;
  margin-top: 20px;
  line-height: 28px;
  // padding-bottom: 10px;
}

.contact__fields input::placeholder,
.contact__fields textarea::placeholder {
  color: #ccc;
  opacity: 1;
}

.contact__fields textarea {
  width: 528px;
  height: 120px;
  resize: none;
}

.contact__fields button {
  width: 150px;
  height: 50px;
  margin-top: 40px;
  background-color: transparent;
  border: 0.75px solid $color-darkgray;
  cursor: pointer;
  align-self: flex-end;
}

.contact__error {
  color: red;
  font-size: 12px;
  width: 241px;
}

.contact__error.message {
  width: 350px;
}

.success {
  color: green;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
}
