@import "settings/colors";
@import "settings/fonts";

.main {
  display: flex;
  height: 900px;

  .main__img {
    height: 100%;
    width: 50%;
    overflow-x: hidden;
    position: relative;

    img {
      position: absolute;
      bottom: 0;
      left: -450px;
      display: block;
      z-index: 2;
    }

  }

  .main__content {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .main__title {
      font-size: 36px;
      text-align: center;
      font-weight: 400;
      margin-bottom: 20px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        background-image: url(../assets/Decoration.svg);
        display: block;
        width: 250px;
        height: 30px;
        top: 130%;
        left: 50%;
        transform: translateX(-50%);
      }

    }

    .main__button {
      margin-top: 80px;
      background-color: transparent;
      text-transform: uppercase;
      text-align: center;
      font-weight: 300;
      font-size: 36px;
      color: $color-darkgray;
      width: 400px;
      height: 120px;
      border: 0.75px solid $color-darkgray;
      cursor: pointer;
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        font-weight: 400;
        border-width: 1.5px;
      }

    }

  }

}